import React, { useState, useEffect } from 'react';
import axios from "axios";

export default function Registration(){

    const [email, setEmail] = useState("");
    const [password, setPassword] = useState("");
    const [register, setRegister] = useState(false);

    const handleSubmit = (e) => {
        e.preventDefault();

        const configuration = {
            method: "post",
            url: "http://localhost:8080/account/register",
            data: {
                email,
                password,
            },
        };

        axios(configuration)
            .then((result) => {
                setRegister(true);
            })
            .catch((error) => {
                error = new Error();
            });

    };


return(
    <div>
    <form onSubmit={(e) => handleSubmit(e)}>
        <input value={email}     onChange={(e) => setEmail(e.target.value)}     name="email"     placeholder={`email`}   />
        <input value={password}  onChange={(e) => setPassword(e.target.value)}  name='password'  placeholder={`password`}/>
        <button type={"submit"}>Register</button>
    </form>
    {register ? (
        <p className="text-success">You Are Registered Successfully</p>
    ) : (
        <p className="text-danger">You Are Not Registered</p>
    )}
    </div>
)

};